.calc {
  margin-top: 80px;
  padding: 80px 15px;
  background-color: $color-light-grey;

  @include tablet {
    padding: 30px 15px;
  }

  h2 {
    text-align: center;
    margin-bottom: 40px;
  }

  &__columns {
    display: block;

    @include tablet {
      max-width: 865px;
      margin: 50px auto 0;
      display: flex;
     
    }
  }

  &__form {
    width: 100%;
    margin-right: 0;

    @include tablet {
      width: 50%;
      margin-right: 20px;
    }
  }


  &__input input,
  &__select .select__input {
    color: $color-grey;
    width: 100%;
    border-radius: 6px;
    border: 1px solid $color-primary;
    padding: 11px 20px;
    line-height: 1em;
    font-size: 1.3rem;
    box-shadow: 0px 3px 6px $color-primary-shadow;
    margin-bottom: 20px;
    background-color: $color-white;
  }

  &__input {
    input {
      &::placeholder {
        color: $color-grey;
      }
    }
  }

  &__select {
    position: relative;

    .select__input {
      position: relative;
      height: 40px;
      vertical-align: middle;
      padding: 13px 20px;
      cursor: pointer;

      &::after {
        content: "";
        background-image: url("../assets/Arrow.svg");
        width: 22px;
        height: 22px;
        display: block;
        position: absolute;
        right: 14px;
        top: 9px;
      }
    }

    .select__dropdown {
      display: none;
      position: absolute;
      z-index: 1;
      background-color: white;
      width: 100%;
      border-radius: 6px;
      border: 1px solid $color-redish;
      overflow: hidden;

      li {
        font-size: 13px;
        padding: 10px 20px;
        cursor: pointer;

        &:hover {
          background-color: $color-light-grey;
        }
      }
    }


    &.open {
      .select__input::after {
        transform: rotate(180deg);
      }

      .select__dropdown {
        display: block;
      }
    }
  }

  .checkbox {
    .consent__text {
      padding-top: 4px;
    }
  }

  &__summary {
    width: 100%;
    margin-left: 0;
    margin-top: 0px;

    @include tablet {
    width: 50%;
    margin-left: 20px;
    display: flex;
    flex-direction: column;
     
    }

    .summary__total,
    .list__item {
      box-shadow: 0px 3px 6px rgba(0, 0, 0, .16);
      border-radius: 6px;
      justify-content: space-between;
      align-items: center;
      color: $color-white;
      font-size: 1.4rem;
      display: none;

      &.open {
        display: flex;
      }
    }

    .summary__total {
      width: 50%;
      align-self: flex-end;
      margin-top: 22px;
      padding: 8px 20px;
      background-color: $color-primary;

      .total__price {
        font-size: 1.28rem;
        font-weight: 700;
      }
    }

    .list__item {
      padding: 6px 20px;
      background-color: $color-green;
      margin-bottom: 8px;

      .item {
        &__price {
          font-weight: 700;
          font-size: 1.4rem;
          margin-left: 150px;
        }
      }
    }
  }
}
